<template>
    <div class="mainHeader" :class="[isMobile?'mobileMainHeader':'']">
        <div class="logo "  >
            <div>
                <h1  style="font-size: 30px;">{{sysName}}</h1>
                <h2 style="margin-top: 5px; font-size: 15px;">{{sysNameEn}}
                </h2>
            </div>
        </div>
        <ul class="nav" v-stopDouble >
            <li
                v-for="(item, index) in navs"
                :key="index"
                @click="gotoDetail(item)"
                :class="{ current: currentMenu.sid == item.sid }"
            >{{ item.label }}</li>
        </ul>
        <ul class="icons" v-stopDouble >
            <li class="user">
                <i class="iconfont iconu502"></i>
                {{userName}}
            </li>
            <li class="line">|</li>
            <li class="zhuxiao"  @click="logout()">
                <i class="iconfont iconzhuxiao1"></i>
                <span >注销</span>
            </li>
        </ul>
     
    </div>
</template>
<script>
export default {
    data() {
        return {
            isMobile : /iPhone|iPad|iPod|Android/i.test(navigator.userAgent),
            sysName:GLOBAL_CONFIG.sysName,

            sysNameEn:GLOBAL_CONFIG.sysNameEn,
            isShowDropdown: false,
            currentMenu: {},
            userName: "",
            navs: [
                { label: "常识", sid: 1, path: "/cspd" },
                { label: "阅读理解", sid: 2, path: "/yylj" },
                { label: "判断推理", sid: 3, path: "/pdtl" },
                { label: "资料分析", sid: 4, path: "/zlfx" },
                { label: "数量关系", sid: 5, path: "/slgx" },
                { label: "申论", sid: 6, path: "/sl" },
               
            ],
            currentDropdownState:"",
         
        };
    },
    created() {
        let token = sessionStorage.getItem(GLOBAL_CONFIG.token);
        if(token){
            this.getUserInfo(token);
        }
        // let userInfo = localStorage.getItem(GLOBAL_CONFIG.userInfo);
        // if (userInfo) {
        //     this.userName = JSON.parse(userInfo).userName;
        // }
        this.gotoDetail(this.navs[0]);

    
    },
    methods: {
        hideDropdownFn() {
            this.currentDropdownState=''
            this.isShowDropdown = false;
        },
        getUserInfo(token){
            this.http.post(GLOBAL_CONFIG.base+"/user/getCurrentUser", {
                    token: token
                })
                .then(res => {
                    if (res && res.data) {
                        let data = res.data.data;
                        localStorage.setItem(GLOBAL_CONFIG.userFlag, data.userFlag);
                        if(localStorage.getItem(GLOBAL_CONFIG.userInfo)){
                            localStorage.removeItem(GLOBAL_CONFIG.userInfo);
                            localStorage.setItem(GLOBAL_CONFIG.userInfo, JSON.stringify(data));
                        }else{
                            localStorage.setItem(GLOBAL_CONFIG.userInfo, JSON.stringify(data));
                        }
                        this.userName = data.userName;
                    }
                });
        },
        gotoDetail(item) {
            this.currentMenu = item;
            let prevPath = this.$route.matched[1]
                ? this.$route.matched[1].path
                : "";
            let curPath = item.path;

            if (prevPath != curPath) {
                this.$router.replace(curPath);
            }
        },
        goTymh(){
            window.location.href=GLOBAL_CONFIG.tymh_url
        },
        logout() {
            this.$confirm("您确定要退出登录吗？", "提示", {
                distinguishCancelAndClose: true,
                closeOnClickModal: false,
                cancelButtonText: "取消",
                confirmButtonText: "确定"
            })
                .then(() => {
                    this.$router.push({path:'door'})
                })
                .catch(error => {});
        }
    }
};
</script>
<style lang="less" scoped>
    .zhuxiao:hover .quit{
            display: block!important;
    }
    .mobile 
    .logoutDropdown {
        z-index: 2999;
        width: 140px;
        background: #141e46;
        position: absolute;
        right: 1px;
        top: 70px;
        padding: 10px 0;
        border: 1px solid #2d92ea;
        li {
            height: 36px;
            line-height: 36px;
            color: #fff;
            padding: 0 20px;
            font-size: 18px;
            cursor: pointer;
            &:hover {
                color: #2d92ea;
            }
            i {
                font-size: 18px;
                margin-right: 8px;
            }
        }
    }
.zhuxiao:hover .quit{
    display: block!important;
}
.mainHeader {
    height: 60px !important;
    line-height: 60px;
    background: #3b639a;
    color: #fff;
    font-size: 18px;
    padding: 0 19px;
    display: flex;
    justify-content: space-between;
    .logo {
        display: flex;
        align-items: center;
        i {
            margin-right: 10px;
            font-size: 32px;
            color: #15a7cb;
        }
        h1 {
            font-size: 24px;
            line-height: 30px;
        }
        h2 {
            font-size: 12px;
            line-height: 14px;
            word-spacing: 2px;
            letter-spacing: 1px;
        }
    }
    .nav {
        height: 100%;
        overflow: hidden;
        display: flex;
        li {
            margin: 0 30px;
            border-bottom: 2px solid rgba(0, 0, 0, 0);
            cursor: pointer;
            position: relative;
            font-size: 20px;
            &:hover {
                color: #22fffe;
            }
            &.current {
                color: #22fffe;
                border-color: #22fffe;
            }
        }
    }
 
    .icons {
        display: flex;
        font-size: 16px;
        align-items: center;
        li {
            height: 28px;
            font-size: 20px;
            line-height: 28px;
            display: flex;
            align-items: center;
            margin: 0 8px;
            cursor: pointer;
            &:nth-child(even) {
                font-size: 14px;
                line-height: 14px;
                height: 14px;
                font-weight: 100;
            }
            i {
                margin-right: 6px;
                font-size: 14px;
            }
        }
        .zhuxiao{
            position: relative;
            .quit{
                padding: 10px;
                box-sizing: border-box;
                display: none;
                background: #141E46;
                height: 80px!important;
                width: 135px;
                position: absolute;
                right: -10px;
                top: 30px;
                border:1px solid #2776C3;
                padding-left: 15px;
                z-index: 9;
            /*// display: block;*/
                div{
                    line-height: 30px;
                    font-size: 18px;
                    cursor: pointer;
                        i{
                            margin-right: 5px;
                        }
                    }
            }
        }
        .adressArea {
            span {
                position: relative;
                z-index: 20;
                .arrowicon {
                    font-size: 12px;
                    display: inline-block;
                    font-weight: 100;
                    margin-left: 10px;
                    transform: rotate(-90deg);
                    transition: 0.2s;
                }
            }
            &.open {
                color: #22fffe;
                .arrowicon {
                    transform: rotate(90deg);
                }
            }
        }
    }
}
.mobileMainHeader {
    padding: 0 0.08rem;
    .logo{
        display: none!important;
    }
    .nav {
        li{
            margin: 0px;
            box-sizing: border-box;
            font-size: 14px;
            margin-right: 0.16rem;
        }
        li:nth-child(1){
            margin-left: 0.13rem;
        }
    }
    .icons{
        width: 30px;
        .user{
            display: none;
        }
        .line{
            display: none;
        }
        .zhuxiao{
            span{
                display: none;
            }
        }
    }
}

</style>
