<template>
    <div class="home" :class="[isMobile?'mobilehome':'']">
        <main-header></main-header>
        <div class="homeRight">
            <router-view v-if="isShowView"></router-view>
        </div>
    </div>
</template>

<script>
import mainHeader from "@common/mainHeader.vue";
export default {
    name: "Home",
    components: {
        mainHeader
    },
    data() {
        return {
            isMobile : /iPhone|iPad|iPod|Android/i.test(navigator.userAgent),
            isShowView: true
        };
    },

    methods: {
        
        reload() {
            this.isShowView = false;
            this.$nextTick(() => {
                this.isShowView = true;
            });
        }
    }
};
</script>
